



let s_result = require('./draw.js').s_result;
export default {
components: {
},
data () {
return {
msg:"Choose One",
number:0,
time:0,
draw:false,
path:'/static/tarot/',
mean:'',
card:'',
style:'',
each:{}
}
},
updated(){
this.initial();
},
mounted(){
this.initial();
},
methods:{
initial(){
let num = this.$route.params.num;
if(num==undefined) return;
if(num!=1 && num!=3 && num!=5){
alert("Not Allow to Access!")
history.go(-1);
return
}
this.number = num;
},
leaveHandler(){
history.back();
},
tStart(e){
this.time = e.timeStamp;
this.size+=50;
},
tEnd(e){
let touchTime = (e.timeStamp-this.time)/1000;

if(touchTime<2) return
this.msg="Go Back"
console.log(touchTime);


let d = s_result(this.number);
for(let i=0;i<this.number;i++){
this.each[i] = {};
if(d[i].reversed) this.each[i].style='transform: scaleY(-1);'
this.each[i].path = d[i].path;
this.each[i].card = d[i].card;
}
this.draw=true
}
}
}



